.progress {
  background-image: -webkit-linear-gradient(top, #dde3e8 0%, #e9edf0 100%);
  background-image: -o-linear-gradient(top, #dde3e8 0%, #e9edf0 100%);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#dde3e8), to(#e9edf0));
  background-image: linear-gradient(to bottom, #dde3e8 0%, #e9edf0 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffdde3e8', endColorstr='#ffe9edf0', GradientType=0);
}
.progress-bar {
  background-image: -webkit-linear-gradient(top, #ff50a0 0%, #ff1d84 100%);
  background-image: -o-linear-gradient(top, #ff50a0 0%, #ff1d84 100%);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ff50a0), to(#ff1d84));
  background-image: linear-gradient(to bottom, #ff50a0 0%, #ff1d84 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffff50a0', endColorstr='#ffff1d84', GradientType=0);
}
.progress-bar-striped {
  background-image: -webkit-linear-gradient(45deg, rgb(255,154,201) 25%, transparent 25%, transparent 50%, rgb(255,154,201) 50%, rgb(255,154,201) 75%, transparent 75%, transparent);
  background-image: -o-linear-gradient(45deg, rgb(255,154,201) 25%, transparent 25%, transparent 50%, rgb(255,154,201) 50%, rgb(255,154,201) 75%, transparent 75%, transparent);
  background-image: linear-gradient(45deg, rgb(255,154,201) 25%, transparent 25%, transparent 50%, rgb(255,154,201) 50%, rgb(255,154,201) 75%, transparent 75%, transparent);
}


